<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ products.length }} товара </span>
    </div>

    <section class="cart">
      <div class="preloader" v-if="cartProductsLoading">
        Загрузка товаров, добавленных в корзину...
        <img src="img/preloader.gif" />
      </div>
      <div v-else-if="cartProductsLoadingFailed">
        Произошла ошибка при загрузке корзины
        <button @click="loadCart">Попробовать еще раз</button>
      </div>
      <form
        v-else
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="addToCart"
      >
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem
              v-for="item in products"
              :key="item.productId"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link
            v-if="products.length > 0"
            tag="button"
            :to="{ name: 'order' }"
            class="cart__button button button--primery"
            type="submit"
          >
            Оформить заказ
          </router-link>
          <div v-else>
            Сначала добавьте в корзину товар
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem";

export default {
  components: { CartItem },
  data() {
    return {
      productAmount: 1,
      cartProductsLoading: false,
      cartProductsLoadingFailed: false,
    };
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
  methods: {
    addToCart() {
      this.$store.commit("addProductToCart", {
        productId: this.product.id,
        amount: this.productAmount,
      });
    },
    loadCart() {
      this.cartProductsLoading = true;
      this.cartProductsLoadingFailed = false;

      clearTimeout(this.loadCartTimer);

      this.loadCartTimer = setTimeout(() => {
        this.cartProductsLoading = false;
      }, 5000);
    },
  },
  created() {
    this.loadCart();
  },
  filters: { numberFormat },
};
</script>
