<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="Убрать один товар"
      @click.prevent="minusButton()"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <input type="text" v-model.number="currentProductAmount" />

    <button
      type="button"
      aria-label="Добавить один товар"
      @click.prevent="plusButton()"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ["productAmount"],
  data() {
    return {
      currentProductAmount: this.productAmount,
    };
  },
  watch: {
    productAmount(value) {
      this.currentProductAmount = value;
    },
  },
  methods: {
    minusButton() {
      if (this.currentProductAmount > 1) {
        this.$emit(
          "update:productAmount",
          (this.currentProductAmount = this.currentProductAmount - 1)
        );
      }
    },
    plusButton() {
      this.$emit(
        "update:productAmount",
        (this.currentProductAmount = this.currentProductAmount + 1)
      );
    },
  },
};
</script>
